<template>
    <b-container>
    <div class="accordion" role="tablist">
    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.accordion1 variant="info">未完成卡片</b-button>
        </b-card-header>
        <b-collapse id="accordion1" visible accordion="my-accordion" role="tabpanel">
            <b-card-body>
                <b-card-group deck>
                <HintCard
                    v-for="hint in undone_hints"
                    :key="hint.id"
                    :id="hint.id"
                    :name="hint.name"
                    :status="hint.done"
                >
                </HintCard>
                </b-card-group>
            </b-card-body>
        </b-collapse>
    </b-card>

    <b-card no-body class="mb-1">
        <b-card-header header-tag="header" class="p-1" role="tab">
            <b-button block v-b-toggle.accordion-2 variant="info">已完成卡片</b-button>
        </b-card-header>
        <b-collapse id="accordion-2" accordion="my-accordion" role="tabpanel">
            <b-card-body>
                <b-card-group deck>
                <HintCard
                    v-for="hint in done_hints"
                    :key="hint.id"
                    :id="hint.id"
                    :name="hint.name"
                    :status="hint.done"
                >
                </HintCard>
                </b-card-group>
            </b-card-body>
        </b-collapse>
    </b-card>
    </div>

    </b-container>


</template>

<script>
import HintCard from "../components/HintCard.vue";
// @ is an alias to /src

export default {
    name: "Home",
    components: {
        HintCard,
    },
    mounted() {
        this.group_id = this.$route.query.group;
        this.fetchData();
    },
    data() {
        return {
        undone_hints: [],
        done_hints: [],
        group_id: Number,
        };
    },
    methods: {
        async fetchData() {
            const res = await fetch("hints.json", { mode: "no-cors" });
            const val = await res.json();
            console.log(val);

            for (var i = 0; i < val.length; ++i) {
                if (val[i].done === "no") this.undone_hints.push(val[i]);
                else this.done_hints.push(val[i]);
            }
        },
    },
};
</script>
